const getGoodsData = async goodsStorageName => {
    let localStorageValue = localStorage.getItem(goodsStorageName);

    if (localStorageValue) {
        return JSON.parse(localStorageValue);
    }

    const res = await fetch('data/data.json');
    localStorageValue = await res.json();
    localStorage.setItem(goodsStorageName, JSON.stringify(localStorageValue));

    return localStorageValue;
};
function mainEventHandler(event) {
    const {goodsData, goodsInCartData} = event.data;
    const target = event.target;
    let dataInfo = target.dataset.info;
    let itemId = parseInt(target.dataset.itemId);
    // when click isn't on basket element, basket should be hidden
    if (!(target.className).includes("basket")) {
        $(".basket").addClass("hide");
    }
    switch (dataInfo) {
        case 'home':
            showMainAndCategoryPage(goodsData) // to render all items
            break;
        case 'laptop':
        case 'phone':
        case 'TV':
            showMainAndCategoryPage(goodsData, dataInfo); // to render certain category items
            break;
        case 'add':
        case 'show_cart':
            //add to cart function;
            showCartWithGoods(itemId, goodsInCartData);
            //они будут работать вместе, т.к. мы показываем корзину каждый раз когда добавляем товар в нее
            break;
        case 'remove':
            //remove from cart function;
            break;
        case 'increase':
            //increase q-ty of item in cart function;
            break;
        case 'decrease':
            //decrease q-ty of item in cart function;
            break;
        case 'show_product':
            showGoodsOnItemPage(goodsData, itemId);
            break;
        //TO-DO: need also add events for filter and sorting
    }

}
const showMainAndCategoryPage = (goodsData, category) => {

    $("section.product, section.comments").addClass("hide");
    $("li.catalog__item").remove();
    $(".carousel, section.catalog, aside.filters, .page-title-name").removeClass("hide");

    // add special data attr for filter block
    $("main").attr("data-current-category", category);
    if (!category) {
        $("main").removeAttr("data-current-category");
    }

    // clear filters when category is choosen
    $(".filters__form").trigger("reset");

    let pageTitleName = $(".page-title-name");
    switch (category) {
        case "laptop":
            pageTitleName.text("НОУТБУКИ");
            break;
        case "TV":
            pageTitleName.text("ТЕЛЕВИЗОРЫ");
            break;
        case "phone":
            pageTitleName.text("ТЕЛЕФОНЫ");
            break;
        default:
            pageTitleName.text("ВСЕ ТОВАРЫ");
            break;
    }

    renderGoods(goodsData, category);
    filterGoods(goodsData);
};
function renderGoods (goodsData, category) {
    const catalogList = $('.catalog__list');
    goodsData.forEach(good => {
        if (category && good.category !== category) {
            return;
        }
        const {id, img, name, brand, price, available} = good;
        const availableMessage = '<span class="catalog__text--available">(Есть в наличии)</span>';
        const unavailableMessage = '<span class="catalog__text--unavailable">(Нет в наличии)</span>';

        catalogList.append(`
            <li class="catalog__item" data-item-id=${id} data-available=${available}>
                <div class="catalog__item-browser"></div>
                <a href="#" data-item-id=${id}>
                    <img class="catalog__img" data-info="show_product" src=${img} alt="${name}" data-item-id=${id}>
                </a>
                <div class="catalog__item-hide">
                    <h3 class="catalog__title">${brand}</h3>
                    <p class="catalog__text">${name}</p>
                    <p>${available ? availableMessage : unavailableMessage}</p>
                    <div class="catalog__btn btn" data-item-id=${id} data-info="add">${price} грн</div>
                </div>
            </li>
        `);
    });
}
const showGoodsOnItemPage = (goodsData, itemId) => {
    let itemDesc = $(".product__table");

    let newItem = goodsData.find(good => {
        if (good.id === itemId) {
            return good;
        }
    });

    // hide slider, filters and categories
    $("section.catalog, aside.filters, .carousel, .page-title-name").addClass("hide");

    // show product and comments
    $("section.product, section.comments").removeClass("hide");

    // clear previous products data
    $(".product__row").remove();

    $(".product__title").text(`${newItem.brand} ${newItem.category} ${newItem.name}`);
    $(".product__img").attr({"src": `${newItem.img}`, "alt": `${newItem.name}`});
    $(".product__price-text").text(`${newItem.price}`);
    $("button.product__btn").attr("data-item-id",`${newItem.id}`);

    // draw item description properties
    for (let key in newItem) {
        if (key !== "price" && key !== "img" && key !== "comments") {
            itemDesc.append(`
            <div class="product__row">
                <div class="product__col product__col--properties">${key}</div>
                <div class="product__col product__col--values">${newItem[key]}</div>
            </div>`);
        }
    }

    // clear previous comments
    $(".comments__left ul li").remove();

    $("button.comments__btn").attr("data-item-id", `${newItem.id}`)

    if (newItem.comments.length > 0) {
        newItem.comments.forEach(comment => {
            let newComment = $(`<li>${comment.user} left comment: <br /> "${comment.text}"</li>`);
            $(".comments__left ul").append(newComment);
        })
    } else {
        $(".comments__left ul").append($(`<li>Новых комментариев нет.<br />Оставьте свой первым</li>`));
    }

}

function showCartWithGoods (dataItemId, goodsInCartData) {
    $(".basket").removeClass("hide");
    if (dataItemId) {
        if (goodsInCartData) {
            goodsInCartData.forEach( good => {
                let newGood = $(`
                <tr data-item-id=${good.id}>
                    <td class="basket__delete" data-item-id=${good.id}>
                        <button data-item-id=${good.id} data-info="remove">Удалить</button>
                    </td>
                    <td class="basket__pic" data-item-id=${good.id}>
                        <img src=${good.img} width="33" height="33" alt=${good.name} data-item-id=${good.id} data-info="show_product">
                    </td>
                    <td class="basket__name">${good.name}</td>
                    <td class="basket__price">
                        <div class="basket__spinner">
                            <div class="basket__minus" data-info="decrease">-</div>
                            <div class="basket__count">1</div>
                            <div class="basket__plus" data-info="increase">+</div>
                        </div>
                        <span class="basket__single_item_price">${good.price}</span>
                    </td>
                    <td class="basket__value">${good.price} X good.q-ty</td>
                </tr>
                `)
                $(".basket__table tbody").append(newGood);
            })
        }
    }
}
const showFilter = (goodsData, filterName, filterSelector) => {
    const filtersList = $(filterSelector);
    const filterItems = new Set();

    goodsData.forEach(good => {
        const value = good[filterName];

        if (value) {
            filterItems.add(value);
        }
    });

    filterItems.forEach(item => {
        filtersList.append(`
        <li class="filters__item-option">
            <input type="checkbox"
                   name=${filterName}
                   id="filter-${item}"
                   value=${item}
                   class="visually-hidden filters__input filter__input--checkbox">
            <label for="filter-${item}">${item}</label>
        </li>
    `);
    });
};

const showBrandFilter = goodsData => showFilter(goodsData, 'brand', '.filters__list--brands');
const showResolutionsFilter = goodsData => showFilter(goodsData, 'resolution', '.filters__list--resolution');



const filterGoods = goodsData => {
    const filtersSubmitBtn = $('.filters__btn');
    const form = $('.filters__form');
    const goodsCards = $('.catalog__item');

    filtersSubmitBtn.on('click', handleClickOnFilterSubmitBtn);

    function handleClickOnFilterSubmitBtn(e) {
        const filtersValues = createObjWithFilterValues();
        const errorMessage = $('.price-error-message');
        errorMessage.addClass('visually-hidden');

        if (filtersValues.minPrice < 0 || filtersValues.maxPrice < 0) {
            errorMessage.text('Цена должна быть положительной')
                .removeClass('visually-hidden');
            return;
        }

        if (parseInt(filtersValues.minPrice) > parseInt(filtersValues.maxPrice)) {
            errorMessage.text('Минимальная цена должна быть меньше максимальной')
                .removeClass('visually-hidden');
            return;
        }

        goodsCards.each(showGoodsOnPageAccordingToFilters);

        function createObjWithFilterValues() {
            const filtersValues = {
                selectedBrands: [],
                selectedResolutions: []
            };

            const serializedArray = form.serializeArray();

            serializedArray.forEach(({name, value}) => {
                if (name === 'brand') {
                    filtersValues.selectedBrands.push(value);
                    return;
                }

                if (name === 'resolution') {
                    filtersValues.selectedResolutions.push(value);
                    return;
                }

                filtersValues[name] =  value;
            });

            return filtersValues;
        }

        function showGoodsOnPageAccordingToFilters()  {
            const goodsId = parseInt(this.dataset.itemId);
            const goodsCardData = goodsData.find(item => item.id === goodsId);
            const {price, available, brand, resolution, sim, cpu} = goodsCardData;
            const {minPrice, maxPrice, availability, selectedBrands, selectedResolutions,
                selectedCpu, selectedSim} = filtersValues;
            const checkMinPrice = minPrice ? price >= minPrice : true;
            const checkMaxPrice = maxPrice ? price <= maxPrice : true;
            const checkAvailability = availability ? Boolean(+availability) === available : true;
            const checkBrand = selectedBrands.length ? selectedBrands.includes(brand) : true;
            const checkResolution = selectedResolutions.length ? selectedResolutions.includes(resolution) : true;
            const checkCpu = selectedCpu ? selectedCpu === cpu : true;
            const checkSim = selectedSim ? selectedSim === sim : true;

            if (checkMinPrice && checkMaxPrice && checkAvailability && checkBrand && checkResolution
                && checkCpu && checkSim) {
                $(this).removeClass('visually-hidden');
            } else {
                $(this).addClass('visually-hidden');
            }
        }
    }
};
(async function() {
    const GOODS_STORAGE_NAME = 'all_goods';
    const CART_STORAGE_NAME = 'goods_in_cart';
    let goodsData = await getGoodsData(GOODS_STORAGE_NAME);
    let goodsInCartData = await getGoodsData(CART_STORAGE_NAME);

    $("body").on("click", {goodsData, goodsInCartData} ,mainEventHandler);
    showMainAndCategoryPage(goodsData);
    showBrandFilter(goodsData);
    showResolutionsFilter(goodsData);

    addNewComments();
})();

const addNewComments = () => {
    $('#commentsForm').submit( (goodsData) => {
        event.preventDefault();

        let name = $('.comments__field--name');
        let area = $('.comments__field--textarea');

        // localStorrage
        let comments = []; 
        let regExp = /\b([A-Z]{1}[a-z]{1,30}[- ]{0,1}|[A-Z]{1}[- \']{1}[A-Z]{0,1}[a-z]{1,30}[- ]{0,1}|[a-z]{1,2}[ -\']{1}[A-Z]{1}[a-z]{1,30}){2,5}/;

        let testName = !(name.val().length === 0) || regExp.test(name.val());
        let textArea = !(area.val().length === 0) || regExp.test(area.val());

        if ($('.comments__invalid').hasClass('comments__invalid')) {
            $('.comments__invalid').remove();
        }

        if (testName && textArea) {

            // This is have to push in localStorage
            comments.push({
                user: name.val(),
                text: area.val(),
            })


            $('.comments__list').append(`
                <li>
                  <div>${name.val()}</div>
                  <div>${area.val()}</div> 
                </li>`
            );

            name.val('');
            area.val('');

        } else {
            name.before('<div class="comments__invalid">invalid</div>');
            area.before('<div class="comments__invalid">invalid</div>');
        };

    });
};